"use client";
import { useState, ReactNode } from "react";
import { usePathname } from "next/navigation";
import TopBar from "@/components/ui/TopBar";
import AnnouncementBox from "@/components/ui/AnnouncementBox";

export default function LayoutWrapper({ children }: { children: ReactNode }) {
  const [annVisible, setAnnVisible] = useState(true);
  const pathname = usePathname();

  // Show TopBar and AnnouncementBox only on pages that don't have their own navigation
  // For now, we'll show them everywhere - dashboards have their own headers which work independently
  const showTopChrome = true; // Can be made conditional based on pathname if needed

  return (
    <>
      {showTopChrome && (
        <>
          <TopBar
            onSearch={(q) => {
              console.log("Search query:", q);
              // Implement global search routing here if needed
            }}
            userName="Student"
            userEmail="student@tracksheet.com"
          />
          {annVisible && (
            <AnnouncementBox
              text="üì¢ New: Semester schedule updated. Check your Calendar section for updates."
              cta={{
                label: "View Calendar",
                onClick: () => {
                  console.log("Navigate to calendar");
                  // Implement navigation to calendar
                },
              }}
              onClose={() => setAnnVisible(false)}
            />
          )}
        </>
      )}
      <main className="min-h-screen">{children}</main>
    </>
  );
}
